/**********************
* Base style
**********************/
html,
body {
  height: 100%;
}

body {
  background: #fff;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 14px;
  color: #000;
  font-weight: 400;
  min-width: 320px;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  position: relative;
}

ul {
  list-style: none;
  padding: 0;
}

br {
  font-size: 0;
}

/**********************
* typography
**********************/
h1,
h2,
h3,
h4,
h5,
h6,
p,
hr {
  margin: 0;
  font-weight: 400;
}

a {
  display: inline-block;
}

/******************************************* HEADER *******************************************/
/********** .contacts **********/
.contacts {
  background-color: #f5f7fa;
  line-height: 40px;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.3);
}

.contacts__left {
  font-size: 12px;
}

.contacts__left a {
  color: #506a85;
}

.contacts__left a:nth-child(1) {
  margin-right: 30px;
}

.contacts__left i {
  margin-right: 10px;
}

.contacts__left .fa-envelope-o {
  font-size: 10px;
}

.contacts__left .fa-mobile {
  font-size: 14px;
}

.contacts__social a {
  width: 30px;
  text-align: center;
  transition: all .3s;
}

.contacts__social a i {
  color: #506a85;
  font-size: 12px;
  transition: all .3s;
}

.contacts__social a:hover i {
  color: #fff;
}

.contacts .contacts__left a:hover {
  color: #00bc9c;
  cursor: pointer;
  text-decoration: none;
}

.contacts__social a:nth-child(1):hover {
  background-color: #4668b3;
}

.contacts__social a:nth-child(2):hover {
  background-color: #de5745;
}

.contacts__social a:nth-child(3):hover {
  background-color: #2fbbed;
}

.contacts__social a:nth-child(4):hover {
  background-color: #d94348;
}

.contacts__social a:nth-child(5):hover {
  background-color: #3079ff;
}

.contacts__social a:nth-child(6):hover {
  background-color: #eb6397;
}

/********** .menu **********/
.menu {
  padding: 20px 0;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.3);
  background-color: #fff;
  transition: all .3s;
}

.menu.active_1 {
  padding: 0;
  z-index: 1000;
  width: 100%;
  position: fixed;
  top: 0;
  opacity: 0.95;
}

.menu.active_1 a.navbar-brand img {
  height: 30px;
  transition: all .3s ease-out;
}

.menu.active_1.active_2 nav.navbar {
  padding: 0;
  transition: all .3s;
}

.menu.active_1.active_2 a.navbar-brand img {
  height: 20px;
  transition: all .3s ease-out;
}

.header .navbar {
  padding-right: 0;
  padding-left: 0;
}

.header .navbar .navbar-brand:hover {
  opacity: 0.7;
}

.navbar .navbar-nav {
  width: 100%;
}

.navbar .navbar-nav .nav-item .nav-link {
  position: relative;
  color: #506a85;
  font-weight: 700;
  font-size: 15px;
  transition: all .3s;
  border-radius: 3px;
  border-top: 1px solid rgba(230, 233, 237, 0);
  border-left: 1px solid rgba(230, 233, 237, 0);
  border-right: 1px solid rgba(230, 233, 237, 0);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  line-height: 50px;
  padding: 0 20px;
  z-index: 101;
  text-transform: uppercase;
}

.navbar .navbar-nav .nav-item:hover a.nav-link {
  color: #00bc9c;
  background-color: #f5f7fa;
  border-color: #e6e9ed;
}

.navbar .navbar-nav .nav-item.hover:hover .hover__menu,
.navbar .navbar-nav .nav-item.hover:hover .hover__menu_mega {
  visibility: visible;
  opacity: 1;
  margin-top: -1px;
}

.navbar .navbar-nav .nav-item .hover_2 {
  position: relative;
}

.navbar .navbar-nav .nav-item .hover_2:hover .hover_2__menu {
  visibility: visible;
  opacity: 1;
  margin-top: -1px;
}

.navbar .navbar-nav .nav-item .hover__menu,
.navbar .navbar-nav .nav-item .hover_2__menu {
  position: absolute;
  display: flex;
  flex-direction: column;
  padding: 0;
  background-color: #f5f7fa;
  border: none;
  width: 210px;
  margin-top: -3px;
  border: 1px solid #e6e9ed;
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
  border-radius: 3px;
  z-index: 100;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  border-bottom-width: 2px;
  border-bottom-color: #ced2d6;
}

.navbar .navbar-nav .nav-item .hover__menu a.hover__item,
.navbar .navbar-nav .nav-item .hover__menu a.hover_2__item,
.navbar .navbar-nav .nav-item .hover_2__menu a.hover__item,
.navbar .navbar-nav .nav-item .hover_2__menu a.hover_2__item {
  font-size: 14px;
  line-height: 45px;
  font-weight: 700;
  padding: 0 20px;
  color: #73879c;
  transition: all .3s;
  border-bottom: 1px solid #e6e9ed;
  display: block;
  text-transform: uppercase;
}

.navbar .navbar-nav .nav-item .hover__menu a.hover__item:hover,
.navbar .navbar-nav .nav-item .hover__menu a.hover_2__item:hover,
.navbar .navbar-nav .nav-item .hover_2__menu a.hover__item:hover,
.navbar .navbar-nav .nav-item .hover_2__menu a.hover_2__item:hover {
  background-color: #00bc9c;
  color: #fff;
  text-decoration: none;
}

.navbar .navbar-nav .nav-item .hover_2__menu {
  top: 0;
  right: -100%;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.5);
}

.navbar .navbar-nav .nav-item .hover__menu_mega {
  position: absolute;
  left: 0;
  display: block;
  padding: 40px;
  padding-top: 30px;
  background-color: #f5f7fa;
  border: none;
  margin-top: -3px;
  border: 1px solid #e6e9ed;
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
  border-radius: 3px;
  z-index: 100;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  border-bottom-width: 2px;
  border-bottom-color: #ced2d6;
}

.navbar .navbar-nav .nav-item .hover__menu_mega h5 {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 25px;
  text-transform: uppercase;
  color: #506a85;
  letter-spacing: 1px;
}

.navbar .navbar-nav .nav-item .hover__menu_mega p {
  font-size: 14px;
  line-height: 22px;
  color: #73879c;
  margin-bottom: 20px;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__focus a {
  position: relative;
  margin-bottom: 25px;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__focus a::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #00bc9c;
  opacity: 0;
  transition: all .3s;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__focus a::after {
  content: '+';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  opacity: 0;
  font-size: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .3s;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__focus a:hover::before {
  opacity: 0.9;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__focus a:hover::after {
  opacity: 1;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__focus button {
  line-height: 40px;
  padding: 0 20px;
  letter-spacing: 0.3px;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__works a {
  position: relative;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__works a::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #00bc9c;
  opacity: 0;
  transition: all .3s;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__works a::after {
  content: '+';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  opacity: 0;
  font-size: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .3s;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__works a:hover::before {
  opacity: 0.9;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__works a:hover::after {
  opacity: 1;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__special ul li {
  position: relative;
  margin-bottom: 5px;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__special ul li::before {
  font-family: Fontawesome;
  content: '\f1db';
  font-size: 8px;
  color: #1abb9c;
  position: absolute;
  top: calc(50% - 5px);
  left: 0;
  color: #506a85;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__special ul li a {
  padding-left: 20px;
  color: #506a85;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__special ul li:hover a {
  color: #00bc9c;
  text-decoration: none;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__special ul li:hover::before {
  color: #00bc9c;
}

.navbar .navbar-nav .nav-item .hover__menu_mega .menu_mega__about button {
  line-height: 40px;
  padding: 0 20px;
  letter-spacing: 0.3px;
}

.navbar .navbar-nav .nav-item .nav-item.hover {
  position: relative;
}

.navbar .navbar-nav .nav-item .hover__menu_search {
  right: 0;
  padding: 15px;
  width: 270px;
}

.navbar .navbar-nav .nav-item .hover__menu_search .form-inline {
  flex-flow: row;
  padding: 5px;
  background-color: #fff;
  border: 1px solid #e6e9ed;
  border-radius: 1px;
}

.navbar .navbar-nav .nav-item .hover__menu_search .form-inline:focus-within {
  border-color: #c9d2da;
}

.navbar .navbar-nav .nav-item .hover__menu_search .form-inline input.form-control {
  border: none;
  outline: none;
  padding: 0 10px;
  width: auto;
  line-height: 30px;
  height: auto;
}

.navbar .navbar-nav .nav-item .hover__menu_search .form-inline input.form-control:focus {
  box-shadow: none;
}

.navbar .navbar-nav .nav-item .hover__menu_search .form-inline input.form-control:focus::placeholder {
  color: #fff;
}

.navbar .navbar-nav .nav-item .hover__menu_search .form-inline input.form-control::placeholder {
  font-size: 16px;
  font-family: 'Source Sans Pro', sans-serif;
  line-height: 22px;
  opacity: .6;
}

.navbar .navbar-nav .nav-item .hover__menu_search .form-inline button {
  width: 35px;
  height: 30px;
  font-size: 18px;
}

/******************************************* MAIN *******************************************/
/********** section.slider **********/
.slider .carousel-inner .carousel-caption {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  padding-top: 0;
  opacity: 0;
  transition: all 1s;
}

.slider .carousel-inner .carousel-caption h5 {
  font-family: 'Lato', sans-serif;
  font-weight: 900;
  font-size: 64px;
  text-transform: uppercase;
  letter-spacing: 4.5px;
  margin-bottom: 15px;
  line-height: 78px;
  animation-duration: 1s;
  animation-delay: 1s;
}

.slider .carousel-inner .carousel-caption p {
  font-size: 22px;
  margin-bottom: 55px;
  letter-spacing: 0.4px;
  line-height: 33px;
  animation-duration: 1s;
  animation-delay: 1.2s;
}

.slider .carousel-inner .carousel-caption button {
  padding: 0 70px;
  font-size: 18px;
  line-height: 60px;
  letter-spacing: 0.45px;
  animation-duration: 1s;
  animation-delay: 1.4s;
}

.slider .carousel-inner .carousel-caption mark {
  background-color: #506a85;
  padding: 15px 35px;
  color: #fff;
  font-size: 60px;
  line-height: 110px;
}

.slider .carousel-inner .carousel-caption.slide_first {
  align-items: center;
  top: -100px;
}

.slider .carousel-inner .carousel-caption.slide_first p {
  color: #a3b1bf;
  max-width: 950px;
}

.slider .carousel-inner .carousel-caption.slide_second {
  align-items: center;
  text-align: right;
  right: 100px;
}

.slider .carousel-inner .carousel-caption.slide_second h5 {
  max-width: 780px;
}

.slider .carousel-inner .carousel-caption.slide_second p {
  max-width: 800px;
  opacity: .8;
}

.slider .carousel-inner .carousel-caption.slide_third {
  align-items: center;
  text-align: left;
  left: 100px;
}

.slider .carousel-inner .carousel-caption.slide_third h5 {
  margin-bottom: 20px;
  max-width: 700px;
}

.slider .carousel-inner .carousel-caption.slide_third p {
  color: #506a85;
  max-width: 700px;
}

.slider .carousel-inner .carousel-caption.slide_fourth {
  top: 8%;
  text-align: right;
  right: 100px;
}

.slider .carousel-inner .carousel-caption.slide_fourth h5 {
  max-width: 760px;
}

.slider .carousel-inner .carousel-caption.slide_fourth p {
  opacity: .8;
}

.slider .carousel-inner .carousel-caption.slide_fifth {
  top: 12%;
}

.slider .carousel-inner .carousel-caption.slide_fifth h5 {
  max-width: 600px;
}

.slider .carousel-inner .carousel-caption.slide_fifth p {
  color: #a3b1bf;
  max-width: 750px;
}

.carousel-item.active .carousel-caption {
  opacity: 1;
}

.carousel-item.active .carousel-caption.slide_first {
  top: 0;
